@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css');
@font-face {
  font-family: 'Indelible';
  src: url(Fonts/Indelible.otf) format('opentype');
}
@font-face {
  font-family: 'VCR';
  src: url(Fonts/VCR.ttf) format('truetype');
}

body {
  background-image: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('img/SYNTH_BG.jpg'); ;
  background-color: #6d6d6d ;
  height: 1080px; /* You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */ 
  background-attachment: fixed;
  background-size: cover; /* Resize the background image to cover the entire container */
}

.navbar {
  background-color: rgb(218, 218, 218);
  font-family: 'VCR', sans-serif;
}

.sticky-nav {
  position: sticky;
  top: 0;
}

.violet-text {
  color: rgb(68, 4, 172) !important;
}

.navbar-brand {
  font-family: 'Indelible', sans-serif;
}

h1 {
  font-family: 'VCR', sans-serif;
  color: white;
}

p {
  font-family: 'VCR', sans-serif;
  color: white;
}

.about-image {
  margin-top: 5vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  height: auto;
  object-fit: cover;
  max-width: 150vh;
  max-height: 75vh;
}

.about-txt {
  text-align: center;
  font-size: 1em;
}

.paragraph-container {
  background-color: rgba(0, 0, 0, 0.5);
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  padding: .5rem;
}

.project-title {
  margin-top: 2vh;
  text-align: center;
}

.project-visual {
  margin-top: 3vh;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 66%;
  height: auto;
  object-fit: cover;
  max-width: 150vh;
  max-height: 75vh;
}

.project-desc {
  text-align: center;
  font-size: 1em;
}